*{
  font-family: 'Courier New', Courier, monospace;
}

body{
  padding: 60px;
}

input{
  width: 50%;
  font-size: 1.2rem;
  color: grey;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
}

.card img{
  max-height: 160px;
}


.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}